/* eslint-disable */
import React, { useState } from 'react';
import cn from 'classnames';
import './App.scss';

import usersFromServer from './api/users';
import categoriesFromServer from './api/categories';
import productsFromServer from './api/products';
import { Table } from './components/Table/Table';

function getCategoryById(id) {
  return categoriesFromServer.find(category => category.id === id);
}

function getUserById(id) {
  return usersFromServer.find(user => user.id === id);
}

const getProducts = (filterByName, filterByCategory, query) => {
  const normalizedQuery = query.trim().toLowerCase();

  let newProducts = productsFromServer.map(product => ({
    ...product,
    category: getCategoryById(product.categoryId),
    owner: getUserById(getCategoryById(product.categoryId)?.ownerId),
  }));

  if (filterByName) {
    newProducts = newProducts
      .filter(product => product.owner.name === filterByName);
  }

  if (filterByCategory.length > 0) {
    newProducts = newProducts
      .filter(product => filterByCategory.includes(product.category.title));
  }

  if (normalizedQuery) {
    newProducts = newProducts
      .filter(product => product.name.toLowerCase().includes(normalizedQuery));
  }

  return newProducts;
};

export const App = () => {
  const [query, setQuery] = useState('');
  const [filterByName, setFilterByName] = useState(null);
  const [filterByCategory, setFilterByCategory] = useState([]);

  const visibleProducts = getProducts(filterByName, filterByCategory, query);

  const resetFilters = () => {
    setFilterByName(null);
    setFilterByCategory([]);
  };

  const addFilteredCategory = (name) => {
    setFilterByCategory(prev => [...prev, name]);
  };

  return (
    <div className="section">
      <div className="container">
        <h1 className="title">Product Categories</h1>

        <div className="block">
          <nav className="panel">
            <p className="panel-heading">Filters</p>

            <p className="panel-tabs has-text-weight-bold">
              <a
                data-cy="FilterAllUsers"
                href="#/"
                onClick={() => setFilterByName(null)}
              >
                All
              </a>

              {usersFromServer.map(user => (
                <a
                  data-cy="FilterUser"
                  href="#/"
                  className={cn({
                    'is-active': user.name === filterByName,
                  })}
                  onClick={() => setFilterByName(user.name)}
                >
                  {user.name}
                </a>
              ))}
            </p>

            <div className="panel-block">
              <p className="control has-icons-left has-icons-right">
                <input
                  data-cy="SearchField"
                  type="text"
                  className="input"
                  placeholder="Search"
                  value={query}
                  onChange={event => setQuery(event.target.value)}
                />

                <span className="icon is-left">
                  <i className="fas fa-search" aria-hidden="true" />
                </span>

                <span className="icon is-right">
                  {/* eslint-disable-next-line jsx-a11y/control-has-associated-label */}
                  <button
                    data-cy="ClearButton"
                    type="button"
                    className="delete"
                  />
                </span>
              </p>
            </div>

            <div className="panel-block is-flex-wrap-wrap">
              <a
                href="#/"
                data-cy="AllCategories"
                className="button is-success mr-6 is-outlined"
                onClick={() => setFilterByCategory([])}
              >
                All
              </a>

              {categoriesFromServer.map(category => (
                <a
                  data-cy="Category"
                  className={cn('button mr-2 my-1', {
                    'is-info': filterByCategory.includes(category.title),
                  })}
                  href="#/"
                  key={category.id}
                  onClick={() => addFilteredCategory(category.title)}
                >
                  {category.title}
                </a>
              ))}
            </div>

            <div
              className="panel-block"
              onClick={resetFilters}
            >
              <a
                data-cy="ResetAllButton"
                href="#/"
                className="button is-link is-outlined is-fullwidth"
              >
                Reset all filters
              </a>
            </div>
          </nav>
        </div>

        <div className="box table-container">
          <p data-cy="NoMatchingMessage">
            No products matching selected criteria
          </p>

          <Table products={visibleProducts} />
        </div>
      </div>
    </div>
  );
};
